<template>
  <div class="main-inner archive posts-collapse">
    <div class="post-block">
      <div class="post-content" 
      v-infinite-scroll="updateList"
      infinite-scroll-disabled="busy"
      infinite-scroll-distance="-100"
      >
        <!-- <div class="collection-title">
          <span class="collection-header">嗯..! 目前共计 3 篇日志。 继续努力。</span>
        </div> -->

        <item v-for="(article) of articles" :key="article.id" :article="article" />
      </div>
      <div class="collection-title">
          <span class="collection-header">{{dataNomore ? "--我也是有底线的--" : "加载中..."}}</span>
      </div>
    </div>
  </div>
</template>

<script>
import Item from "./Item.vue";
import {getList} from "@/api/blog"
import infiniteScroll from 'vue-infinite-scroll'
import defaultSettings from "@/settings";

export default {
  name: "Home",
  directives: {infiniteScroll},
  components: {
    Item,
  },
  data() {
    return {
      name: "Home",
      // loading: true,
      dataLoadStop: false,  //列表数据加载停止状态
      busy: false,    //列表数据加载是否完成
      pageSize: defaultSettings.pageSize,
      page: 0,
      pageCount: 0,
      dataNomore:false,
      articles: [],
      timeout: 200,
    };
  },
  mounted() {
    // this.updateList()
  },
  methods:{
    updateList() {
      //锁定数据加载状态
      this.busy = true;
      setTimeout(() => {
      getList({
        page: this.page + 1,
        pageSize: this.pageSize
      }).then( response => {
        this.articles.push(...response.data.dataList)
        this.page = response.data.pageNo;
        this.pageCount = response.data.totalPage;
        if (response.data.hasNextPage) {
            this.busy = false
        } else{
            this.dataNomore = true
        }
      }).catch( error => { // 请求失败处理
          this.busy = false
          if (this.timeout <= 5000) {
            this.timeout += 1000;
          }
      });
      }, this.timeout)
    }
  }
};
</script>

<style  scoped>
.post-block {
  padding: 0;
}

.posts-collapse .post-content .post-header::before {
  width: 0.7em;
  height: 0.7em;
  top: 0.6em;
  left: -0.5em;
}
.posts-collapse .post-content .post-header {
  border: none;
}
.posts-collapse .post-content .post-header:hover {
  border: none;
}
.collection-title {
  display: flex;
  justify-content: center;
}
</style>
