<template>
  <article itemscope="" itemtype="http://schema.org/Article">
    <header class="post-header">
      <div>
        <time>{{article.releaseTime}}</time>
      </div>
      <div class="title">
        <router-link
          :to="{
            name: 'blogdetail',
            params: {
              id: article.id,
            },
          }"
        >
           {{ article.title }}
        </router-link>
      </div>

      <span class="article-meta">
        <font-awesome-icon fixed-width icon="book" style="" />
        <!-- <span class="">分类</span> -->
        <router-link
          :to="{
            path:'/blog/listbycat',
            query: {
              id: article.category.id,
            },
          }"
          class="badge-pill">
          <span itemprop="name">{{article.category.name}}</span>
        </router-link>

        <font-awesome-icon fixed-width icon="tags" style="margin-left: 2em" v-if="article.tags && article.tags.length > 0" />
        <!-- <span class="" >标签</span> -->
        <router-link 
          v-for="(tag) of article.tags" 
          :key="tag.id"
          :to="{
            path:'/blog/listbytag',
            query: {
              id: tag.id,
            },
          }"
          class="badge-pill">
          <span itemprop="name">{{tag.name}}</span>
        </router-link>
      
      </span>
    </header>
  </article>
</template>

<script>
export default {
  name: "Item",
  props: ["article"],
  data() {
    return {
      name: "Item",
    };
  },
  computed: {},
};
</script>

<style  scoped>
time {
  font-size: 0.8125em;
}
.article-meta {
  font-size: 0.8125em;
}
.title a {
  font-size: 1.375em;;
  border: none;
}
.title a:hover {
  border-bottom: 1px solid #222;
}
.badge-pill {
  margin-left: 0.3em;
  padding: 0 0.6em;
  border-radius: 10rem;
  border: 1px solid #222222;

  background: #ffffff;
}
.badge-pill:hover {
  background: #222222;
  color: #ffffff;
}

.posts-collapse .post-content .post-header::before {
  width: 0.7em;
  height: 0.7em;
  top: 0.6em;
  left: -0.5em;
}
.posts-collapse .post-content .post-header {
  border: none;
}
.posts-collapse .post-content .post-header:hover {
  border: none;
}
</style>
