import request from '@/utils/request'

export function getList(params) {
  return request({
    url: '/wblog/api/articles',
    method: 'get',
    params
  })
}

export function getListByCat(params) {
  return request({
    url: '/wblog/api/articlesbycat',
    method: 'get',
    params
  })
}

export function getListByTag(params) {
  return request({
    url: '/wblog/api/articlesbytag',
    method: 'get',
    params
  })
}

export function getBlogDetail(id) {
  let url = '/wblog/api/articles/' + id
  return request({
    url: url,
    method: 'get'
  })
}

